.circular {
  width: 80px;
  height: 80px;
  margin: 20px;
  border-radius: 50%;
  background: rgb(63, 81, 181);
  box-shadow: 0 0 0 rgba(64, 82, 181, 0.4);
}

.success {
  background: greenyellow;
  border-color: greenyellow;
}

.failure {
  background: red;
  border-color: red;
}

.pulse {
  animation: pulse 2s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(64, 82, 181, 0.4);
  }
  70% {
    -webkit-box-shadow: 0 0 0 10px rgba(64, 82, 181, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(64, 82, 181, 0);
  }
}

@keyframes pulse {
  0% {
    -moz-box-shadow: 0 0 0 0 rgba(64, 82, 181, 0.4);
    box-shadow: 0 0 0 0 rgba(64, 82, 181, 0.4);
  }
  70% {
    -moz-box-shadow: 0 0 0 14px rgba(64, 82, 181, 0);
    box-shadow: 0 0 0 14px rgba(64, 82, 181, 0);
  }
  100% {
    -moz-box-shadow: 0 0 0 0 rgba(64, 82, 181, 0);
    box-shadow: 0 0 0 0 rgba(64, 82, 181, 0);
  }
}
